import React from 'react';

export default function Basket(props) {
    const {cartItems,onAdd,onRemove} = props;
    const itemPrice = cartItems.reduce( (a,c) => a + c.Price * c.qty,0);
    const taxPrice = itemPrice * 0.14;
    const shippingPrice = itemPrice>2000 ? 0 : 50;
    const totalPrice = itemPrice + taxPrice + shippingPrice;
    return <aside className="block col1">
        <h2>Cart Items</h2>
        <div>{ cartItems.length === 0 && <div>Cart is Empty</div>}</div>
        {cartItems.map((item) => (
        <div key={item.id} className="row">
            <div className="col2">{item.name}</div>
            <div className="col2">
            <button className="add" onClick={() => onAdd(item)}>+</button>
      
        <button className="remove" onClick={() => onRemove(item)}>-</button>
        </div>
        <div className="col2 text-right">{item.qty} x ${item.Price.toFixed(2)}</div>
    </div>
    ))}
        {cartItems.length !== 0 && (
          <>
          <hr></hr>
          <div className="row">
          <div className="col2">Items Price</div>
          <div className="col1 text-right">${itemPrice.toFixed(2)}</div>
          </div>
          <div className="row">
          <div className="col2">Tax</div>
          <div className="col1 text-right">${taxPrice.toFixed(2)}</div>
          </div>
          <div className="row">
          <div className="col2">Shipping</div>
          <div className="col1 text-right">${shippingPrice.toFixed(2)}</div>
          </div>
          <div className="row">
          <div className="col2"><strong>Total</strong></div>
          <div className="col1 text-right"><strong>${totalPrice.toFixed(2)}</strong></div>
          </div>
          <hr></hr>
          <div className="row">
              <button>CheckOut</button>
          </div>
          
          </>
        )}
        
    </aside>
}